<template>
  <div>
    <p>Home page</p>
    <p>message:{{ randomNumber }}</p>
    <ul>
      <li v-for="item in items" :key="item.id">
        {{item.message}}
      </li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      randomNumber: 'message',
      items: [
        {message: 'item1'},
        {message: 'item2'}
      ]
    }
  },
  methods: {
    getRandom () {
      const path = 'http://localhost:5000/api/random'
      axios.get(path)
        .then(response => {
          this.randomNumber = response.data.randomNumber
        })
        .catch(error => {
          console.log(error)
        })
    }
  },
  created () {
    this.getRandom()
  }
}
</script>



